<template lang="pug">
    v-card 
        v-toolbar
            v-toolbar-title 定投策略管理
            v-spacer
            v-btn(icon @click="refresh")
                v-icon mdi-refresh

        v-list
            timrunstatuslistitem(
                v-for="ex in exConfigs"
                :key="ex.filename"
                :fileName="ex.filename"
                :buymoney="ex.buymoney"
                :buyamount="ex.buyamount"
                :profit="ex.currencyprofit"
                :sepminutes="ex.sepminutes"
                :status="ex.RunStatus"
                :donecycle="ex.donecycle"
                :minmoney="ex.minonehandmoney"
                :maxmoney="ex.maxonehandmoney"
            )
            

</template>

<script>
import * as api from "@/apis/wswebapi"
import { get} from 'vuex-pathify'
export default {
    data: () => ({
    exConfigs: [],
    ExName:"WKJ"
    }),
    computed:{
        isLogin:get('user/islogin'),
        exbotisexpire:get('user/exbotisexpire'),
    },
    methods:{
        refresh(){

            
            

            api.TIMGetAll().then(res=>{
                if (res.code==200){
                    this.exConfigs=res.data;
                }
                
                
            })
        }
    },

    components: {
      timrunstatuslistitem: () => import('@/views/tim/timComponents/runstatuslistitem'),
    },
}
</script>